      </div>
        
    </div> -->
    <div class="blog__container">
      <!-- <p class="blog__container--title">{{this.blog[this.id].title}}</p> -->
      <div class="blog__container--content">
        <div class="blog__container--content-img" :style="{'background-image': 'url('+this.blog[this.id].img+')'}"></div>       
        <div class="blog__container--content-search">
          <SearchBox  @gotoArticle="gotoArticle" :current="this.id"/>
        </div>
        <p class="blog__container--content-txt" v-html="this.blog[this.id].text"></p>
      </div>
    
    </div>
  </section>
</template>
<script>
import SearchBox from '@/components/SearchBox.vue'
export default {
  components: {SearchBox},
  data() {
    return {
      blog: null,
      id: 0,
    }
  },
  beforeMount() {
    this.blog = [...this.$store.state.data.blog];
    this.id = Number(this.$route.params.postId);
    window.scrollTo({top: 0});
  },
  mounted () {},
  methods: {
    gotoArticle(id) {
      this.index = 0;
      this.id = id;
      this.$router.push(`/blog/${this.id}`);
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    },
    selectArticle(idx) {
      if(idx != this.index) {
        this.index = idx;
        this.$router.push(`/blog/${this.id}`);
      }
    }
  }
}
</script>
