<template>
  <div class="search">
    <div class="search--in">
      <input class="search--in-input" type="text"
        @keyup="keyUp"
      >
      <img class="search--in-icon" src="@/assets/imgs/search.svg" alt="in icon">  
    </div>
    <div class="search--thumbs" v-if="showedBlogs && showedBlogs.length > 0">
      <p class="search--thumbs-txt">Você também irá gostar de ler</p>
      <hr>
      
      <div class="search--thumbs-container">
        <div v-for="(blog, idx) in showedBlogs"
          class="search--thumbs-thumb" :key="idx"
          @click="clickThumnb(blog.id)"
          :class="{'search--thumbs-thumb__selected': blog.id === current}"
        >
          <div
            class="search--thumbs-thumb_img"
            :style="{'background-image': 'url('+blog.img+')'}"
          ></div>
          <p class="search--thumbs-thumb_txt">{{`${blog.title}`}}</p>
        </div>
      </div>
    </div>
    <div v-else class="search--without-thumbs">
      <p class="search--without-thumbs_txt">Sem resultados para a pesquisa</p>
    </div>
  </div>
</template>
<script>
export default {
  props: {current: Number},
  data() {
    return {
      blogs: null,
      showedBlogs: null,
    }
  },
  beforeMount () {
    this.showedBlogs = this.blogs = [...this.$store.state.data.blog];
  },
  methods: {
    keyUp ({target}) {
      this.showedBlogs = this.blogs.filter(
        blog =>  blog.title.toLowerCase().includes(target.value.toLowerCase())
      )
    },
    clickThumnb(id) {
      this.$emit('gotoArticle', id)
    }
  }
}
</script>
